/* tab css */
.tab {
    display: flex;
    flex-direction: column;
    flex: 1 1;
    max-width: 860px;
    align-self: center;
    height: 100%;
  }
  .tab>div.tab-content {
    display: flex;
    flex: 1 1 auto;
    height: 3rem; /* 2019-7-2：不知道为什么，只有加了这个，不管是xrem，tab的滚动才成立 */
  }
  .tab>div.tab-tabs {
    display: flex;
    align-items: center;
  }
  .tab-sm>div.tab-tabs {
    height: 2.5rem;
  }
  .tab-md>div.tab-tabs {
    height: 3.2rem;
  }
  .tab-lg>div.tab-tabs {
    height: 4rem;
  }
  .tab>div.tab-tabs, .tab>div.tab-tabs>div {
    display: flex;
    align-items: center;
  }
  .tab>div.tab-tabs>div {
    flex-direction: column;
    cursor: pointer;
  }
  .tab>div>div {
    overflow-x: hidden;
    overflow-y: auto;
    width: 100%;
  }
  .tab>div>div>div {
    position: relative;
  }
  .tab>div>div>div>u {
    position: absolute;
    right: -0.8rem;
    top: 0rem;
    font-size: 0.7rem;
    background-color: red;
    display: flex;
    align-items: center;
    color: white;
    min-width: 1rem;
    padding: 0 3px;
    height: 1.0rem;
    text-decoration: none;
    justify-content: center;
    border-radius: 0.6rem;
  }
  .tab>div>div>div>u.dot {
    width: 0.5rem;
    height: 0.5rem;
    min-width: 0.5rem;
    right: -0.8rem;
  }
  
  
ul.va-list {
    list-style: none;
    background-color: #f0f0f0;
    padding: 0;
    margin-bottom: -1px;
}
ul.va-list-top-border {
    padding-top: 1px;
}
ul.va-list>li {
    display: flex;
    margin-bottom: 1px;
    background-color: white;
}
ul.va-list>li.va-list-gap {
    background-color: transparent;
}
ul.va-list>li>div {
    flex: 1 1;
    display: flex;
    flex-direction: row;
}
ul.va-list>li.va-row-clickable {
    cursor: pointer;
}
ul.va-list>li>label {
    display: flex;
    flex:1 1;
    flex-direction: row;
    align-items: center;
    margin: 0 0 0 15px;
}
ul.va-list>li>label>label {
    display: flex;
    margin: 0 -15px 0 0;
    align-items: center;
}
ul.va-list>li>label>div {
    display: flex;
    flex:1 1;
}

li.va-list-before {
    color: green;
    font-size: 80%;
    padding: 8px 16px;
}
li.va-list-loading {
    color: black;
    font-size: 80%;
    padding: 1.5rem 1rem;
}
li.va-list-none {
    color: #e0e0e0;
    font-size: 80%;
    padding: 8px 16px;
}
li.va-list-header {
    padding: 4px 16px;
}
li.va-list-footer {
    padding: 4px 16px;
}

